<template>
  <div class="log-wrapper gg-flex-1 gg-flex-2">
    <div class="log-header gg-flex-3">
      <div class="log-header-search gg-flex-3">

        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">systemName：</div>
          <el-input v-model="ruleForm.systemName" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">modelName：</div>
          <el-input v-model="ruleForm.modelName" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">userId：</div>
          <el-input v-model="ruleForm.userId" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">deviceInfo：</div>
          <el-input v-model="ruleForm.deviceInfo" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord1：</div>
          <el-input v-model="ruleForm.keyWord1" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord2：</div>
          <el-input v-model="ruleForm.keyWord2" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord3：</div>
          <el-input v-model="ruleForm.keyWord3" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord4：</div>
          <el-input v-model="ruleForm.keyWord4" placeholder="请输入内容"></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">message：</div>
          <el-input v-model="ruleForm.message" placeholder="请输入内容"></el-input>
        </div>
        <!-- <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">pageSize：</div>
          <el-select v-model="ruleForm.pageSize" placeholder="请选择">
            <el-option v-for="item in pageArr" :key="item" :label="item" :value="item">
            </el-option>
          </el-select>
        </div> -->

        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">选择时间段：</div>
          <el-select v-model="timeLimit" placeholder="请选择">
            <el-option v-for="item in timeList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
        <el-button class="log-header-search-Submit" type="primary" @click="onSubmit">查询</el-button>

      </div>
    </div>

    <div class="log-main">
      <el-table :data="tableList[pageIndex]" height="100%" border style="width: 100%">
        <!-- <el-table :data="tableList[pageIndex]" height="200" border style="width: 100%"> -->
        <el-table-column prop="systemName" label="systemName" width="180">
        </el-table-column>
        <el-table-column prop="modelName" label="modelName" width="180">
        </el-table-column>
        <el-table-column prop="userId" label="userId" width="180">
        </el-table-column>
        <el-table-column prop="deviceInfo" label="deviceInfo">
        </el-table-column>
        <el-table-column prop="keyWord1" label="keyWord1">
        </el-table-column>
        <el-table-column prop="keyword2" label="keyword2">
        </el-table-column>
        <el-table-column prop="keyword3" label="keyWord3">
        </el-table-column>
        <el-table-column prop="keyWord4" label="keyWord4">
        </el-table-column>
        <el-table-column prop="message" label="message">
        </el-table-column>
        <!-- <el-table-column prop="id" label="id" width="180">
        </el-table-column> -->
        <el-table-column prop="createTime" label="日期" width="180">
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
            <!--         <el-button type="text" size="small">编辑</el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="log-footer gg-flex-1">
      <el-pagination ref="pagination" @size-change="handleSizeChange" :page-size='pageSize' @current-change="handleCurrentChange" :current-page="pageIndex" :page-sizes="pageArr" layout="total, sizes, prev, pager, next, jumper" :total="tableData.length">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import qs from 'qs';       //引用qs模块
import axios from 'axios'
export default {
  name: "Log",
  components: {

  },
  data() {
    return {
      // ruleForm: {
      //   name: '',
      //   region: '',
      //   date1: '',
      //   date2: '',
      //   delivery: false,
      //   type: [],
      //   resource: '',
      //   desc: ''
      // },
      timeLimit: '1date',//默认选择时间段 前一天
      pageSize: 10,//默认一页显示条数
      pageIndex: 0,//默认在第一页
      ruleForm: {
        systemName: '',
        modelName: '',
        userId: '',
        deviceInfo: '',
        keyWord1: '',
        keyWord2: '',
        keyWord3: '',
        keyWord4: '',
        message: '',
        beginTime: null,
        endTime: null,
        // pageSize: 20,
        // pageIndex: 1,
      },
      pageArr: [10, 20, 50],
      timeList: [{
        value: '1date',
        label: '最近1天'
      }, {
        value: '3date',
        label: '最近3天'
      }, {
        value: '7date',
        label: '最近7天'
      }, {
        value: '1Month',
        label: '最近1月'
      }, {
        value: '3Month',
        label: '最近三月'
      }],
      tableList: [],
      tableData: [
        //  {"id":32018,"systemName":"静静功能","modelName":"语音会议","userId":"keid","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"发送通知失败","createTime":1635495724012},{"id":32017,"systemName":"静静功能","modelName":"视频会议","userId":"12345","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"成功进入视频房间","createTime":1635495690012},{"id":32016,"systemName":"静静功能","modelName":"视频会议","userId":"12345","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"进入视频房间失败","createTime":1635495668735},{"id":32015,"systemName":"静静功能","modelName":"视频会议","userId":"12345","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"视频会议创建失败","createTime":1635495640298}
      ],
    }
  },
  created() {
    // Array.prototype.in_array = function (e) {
    //   var r = new RegExp(',' + e + ',');
    //   return (r.test(',' + this.join(this.S) + ','));
    // }
    // 初始化
    this.init();
  },
  mounted() {
    window.addEventListener('beforeunload', e => {
      this.beforeunloadFn(e);
    });
  },
  methods: {
    // 监听页面刷新 触发
    beforeunloadFn(e) {
      e = e || window.event;
      this.setLs('RefreshKey', 'true');
    },
    init() {
      // 全局构造函数 添加新方法 创建一个新的数组方法
      Array.prototype.in_array = function (e) {
        var r = new RegExp(',' + e + ',');
        return (r.test(',' + this.join(this.S) + ','));
      } 
      // 页面刷新 赋值  接口查询完成 赋空值
      if (this.getLs('RefreshKey') != '' && this.getLs('queryConditions')) {
        console.log(this.getLs('queryConditions'))
        this.ruleForm = this.getLs('queryConditions');
        this.pageSize = this.getLs('pageObj').pageSize;
        this.pageIndex = this.getLs('pageObj').pageIndex;
        this. timeLimit = this.getLs('pageObj').timeLimit;
        //监听页面刷新 查询
        this.onSubmit();
      }
      this.ruleForm.beginTime=1627807635055;  this.ruleForm.endTime=1635756435055;
     
    }, 
    //点击 查询
    onSubmit() {
      // this.ruleForm.beginTime=1627802485480;  this.ruleForm.endTime=1635751285480;

      // 获取开始 - 结束 时间戳
      this.getTimeChange(this.timeLimit);
      // 查询条件 - 本地存储
      this.setLs('queryConditions', JSON.stringify(this.ruleForm));
      // 翻页 - 页码
      this.setLs('pageObj', JSON.stringify({ pageSize: this.pageSize, pageIndex: this.pageIndex,timeLimit:this.timeLimit }));
       // 监听页面值刷新 赋空值
      this.setLs('RefreshKey', '');

      // 调用查询接口
      console.log(this.ruleForm)
     
      let url= window.location.href;
      console.log( window.location)
      // if(url != 'http://log.woniu.com'){
      //   //  window.location.href = 'http://log.woniu.com';
      //     url == 'http://log.woniu.com';
      // } 
      process
      axios.get("/query", {
        params: this.ruleForm,
      }).then((res) => {
        console.log(res);
        if (res.status = 200) {
          let data = res.data.data.content;
          if (data.length >= 1) {
            console.log(data)
            for (let i in data) {
              data[i].createTime = this.getLocalTime(data[i].createTime);
            }
          }
          // 数据存在本地 翻译留用
          this.tableData = data;
          this.tableList = this.setTableList([], this.pageSize, this.tableData);
        }

        console.log(this.tableList)
        // this.tableData=res
      }).catch((res) => {
        console.log(res);
      })
      // axios({
      //   method: "POST",
      //   url: "/apis/users/queryUserInfo.wn",
      //   data: qs.stringify({ userId: '5bfcdc99aa55498aa1c5da6be672c136' }),
      //   headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8', 'token': 'TGT-6900-pxvMMi3UCbyD4mc350bvUZ2skOf0wAhNBBfJTfqTPuEZTfujSi-oa.sso.woniu.com' }
      // }).then((res) => {
      //   console.log(res);

      // }).catch((res) => {
      //   console.log(res);
      // })
    },
    // 设置 localStorage
    setLs(k, v) {
      var _v;
      if (typeof v === 'string' || typeof v === 'number') {
        _v = v;
      } else {
        _v = JSON.stringify(v);
      }
      try {
        localStorage.setItem(k, _v);
        return true;
      } catch (e) {
        console.log('存储本地数据失败');
        return false;
      }
    },
    // 取出 localStorage
    getLs(k) {
      var str = localStorage.getItem(k);
      try {
        str = JSON.parse(str);
      } catch (e) {
      }
      return str === 'undefined' ? '' : str;
    },
    // 查看详情
    handleClick(row) {
      //   编程式导航-params传递参数-JS代码内部跳转 + 传递参数
      console.log(row);
      this.$router.push({
        // name: "LogDetails",
        path: "/logDetails",
        query: {
          constraints: JSON.stringify(row),
        }
      });
    },
    // 设置每一页显示多少条
    handleSizeChange(val) {
      // 重置 页面显示数据条数
      this.pageSize = val;
      console.log(this.pageSize)
      // 修改数据
      this.tableList = this.setTableList([], this.pageSize, this.tableData);
      console.log(`每页 ${val} 条`);
    },
    // 点击跳转到第几页
    handleCurrentChange(val) {
      // 重置 当前在等几页
      this.pageIndex = (val -= 1);
      // console.log(this.pageIndex)
      // console.log(this.tableList[this.pageIndex])
      console.log(`当前页: ${val}`);
    },
    // 设置一页显示的数据
    setTableList(newArr, numb, arr) {
      newArr = [];
      let arrLength = arr.length; // 数组长度
      let num = numb;  // 每页显示 10 条
      let index = 0;
      for (let i = 0; i < arrLength; i++) {
        if (i % num === 0 && i !== 0) { // 可以被 10 整除
          newArr.push(arr.slice(index, i));
          index = i;
        };
        if ((i + 1) === arrLength) {
          newArr.push(arr.slice(index, (i + 1)));
        }
      };
      console.log(newArr)
      return newArr;
    },
    // 时间戳 转 日期
    getLocalTime(nS) {
      return new Date(parseInt(nS)).toLocaleString().replace(/:\d{1,2}$/, ' ');
    },
    // 监听选择的时间 返回时间戳  
    getTimeChange(selVal) {
      var nowTime = new Date();
      // 结束日期时间戳
      this.ruleForm.endTime = nowTime.getTime();
      switch (selVal) {
        case '1date':
          this.ruleForm.beginTime = this.getDate(nowTime, 1);
          break;
        case '3date':
          this.ruleForm.beginTime = this.getDate(nowTime, 3);
          break;
        case '7date':
          this.ruleForm.beginTime = this.getDate(nowTime, 7);
          break;
        case '1Month':
          this.ruleForm.beginTime = this.getTime(nowTime, 1);
          break;
        case '3Month':
          this.ruleForm.beginTime = this.getTime(nowTime, 3);
          break;
        default:
      };

      console.log(this.ruleForm.beginTime, this.getLocalTime(this.ruleForm.beginTime))
    },
    //前 index  多少天 (一天的毫秒时间  24 * 60 * 60 * 1000)
    getDate(nowTime, index) {
      return nowTime.getTime() - index * 24 * 60 * 60 * 1000;
    },
    //  获取当前时间到 前几个月的数据 （获取月的天数 * 一天的） 多少天
    getTime(nowTime, index) {
      let begin = "";
      if (nowTime.getMonth() == 1) {//2月
        if (nowTime.getFullYear() % 4 == 0) {//润年
          //润年 一个月 29天
          begin = nowTime.getTime() - 29 * 24 * 3600 * 1000;
        } else {//平年
          // 平年 一个月 28天
          begin = nowTime.getTime() - 28 * 24 * 3600 * 1000;
        }
      } else {
        let month = new Array([2, 4, 6, 9, 11]);
        if (month.in_array(nowTime.getMonth())) {//小月
          // 小月 一个月 30 天
          begin = nowTime.getTime() - 30 * 24 * 3600 * 1000;
        } else {//大月
          // 大月 一个月 31 天
          begin = nowTime.getTime() - 31 * 24 * 3600 * 1000;
        }
      }
      index--;
      if (index == 0) {
        return begin;
      } else {
        return this.getTime(new Date(begin), index);
      };
    },
  },
  // 销毁前状态
  beforeDestroy() {
    console.group("beforeDestroy 销毁前状态 Log===============》");
  },
  destroyed() {
    alert(11)
    console.group("destroyed 销毁完成状态 Log===============》");
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../../assets/css/log.css";
/* h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
} */
</style>



